import React, { Component } from "react";
import { Grid, Cell, List, ListItem, ListItemContent } from "react-mdl";

class Contact extends Component {
  render() {
    return (
      <div className="contact-body">
        <Grid className="contact-grid">
          <Cell col={6}>
            <h2>Juan Carlos Vegas Salazar</h2>
            <div style={{textAlign: 'center'}} className="circular-contact">
                    <img
                    src={require('../assest/img/3.jpg')}
                    alt="avatar"
                    style={{height: '200px'}}
                    />
            </div>
            <p style={{ width: "75%", margin: "auto", paddingTop: "1em" }}>
              Gracias por visitar mi Web, si estas interesado por favor contactame 
              seria un placer unirme a tu equipo y trabajar juntos. Saludos!
            </p>
          </Cell>
          <Cell col={6}>
          
          <h2>Contactame</h2>
          <hr/>
          <div className="contact-list">
          <List>
            <ListItem>
            <ListItemContent style={{fontSize: '30px' , fontFamily: 'Anton'}} >
            <i className="fa fa-phone-square" aria-hidden="true"></i>
            (+56) 9 9161-8080
            </ListItemContent>
            </ListItem>
            <ListItem>
            <ListItemContent style={{fontSize: '30px' , fontFamily: 'Anton'}} >
            <i className="fa fa-fax" aria-hidden="true"></i>
            (2) 2316-2234
            </ListItemContent>
            </ListItem>
            <ListItem>
            <ListItemContent style={{fontSize: '30px' , fontFamily: 'Anton'}} >
            <i className="fa fa-envelope" aria-hidden="true"></i>
            juan_k704@hotmail.com
            </ListItemContent>
            </ListItem>
            <ListItem>
            <ListItemContent style={{fontSize: '30px' , fontFamily: 'Anton'}} >
            <i className="fa fa-skype" aria-hidden="true"></i>
            juank704
            </ListItemContent>
            </ListItem>
          </List>
          </div>
          </Cell>
          
        </Grid>
      </div>
    );
  }
}

export default Contact;
