import React, { Component } from 'react';
import { Grid, Cell } from 'react-mdl'

class Landingpage extends Component {
    render(){
        return (
            <div style={{width: '100%', margin: 'auto'}}>
                <Grid className="landing-grid">
                    <Cell col={12}>
                    <div className="circular">
                    <img 
                        src={require('../assest/img/5.jpg')}
                        alt="avatar"
                        />
                    </div>
                        <div class="banner-text">
                            <h1>Full Stack Web Developer</h1>
                            <hr/>
                            <p> HTML/CSS | Javascript | Java | .NET | React </p>
                            <div className="social-links">
                                <a href="https://www.linkedin.com/in/juancvegas" rel="noopener noreferrer" target="_blank">
                                    <i className="fa fa-linkedin-square" aria-hidden="true"></i>
                                </a>
                                <a href="https://github.com/juank704" rel="noopener noreferrer" target="_blank">
                                    <i className="fa fa-github-square" aria-hidden="true"></i>
                                </a>
                                <a href="https://twitter.com/juan_k704" rel="noopener noreferrer" target="_blank">
                                    <i className="fa fa-twitter-square" aria-hidden="true"></i>
                                </a>
                                <a href="https://www.freecodecamp.org/juank704" rel="noopener noreferrer" target="_blank">
                                    <i className="fa fa-free-code-camp" aria-hidden="true"></i>
                                </a>
                            </div> 
                        </div>
                    </Cell>
                </Grid>
            </div>
        )
    }
}

export default Landingpage;