import React, { Component } from 'react';
import { Grid, Cell } from 'react-mdl';
import FlagIcon from './flagicon'

class Experience extends Component {

render(){ 
    function ItemIcon(props) {
        return (<label> <i className={props.icon} style={{fontSize:'2em'}} ></i> - {props.message} | </label>)
      }

    function ExtraIcons(props) {
        const extraIcon = props.needIcon;
        if (extraIcon) {
          return (<label> <img src={props.refIcon} alt="extraImagen" /> - {props.textIcon} </label>)
        }
        return ""
      }
    
return (
    <div>
        <Grid>
            <Cell col={4}>
            <p>{this.props.startYear} - {this.props.endYear}</p>
            <FlagIcon code={this.props.countryLocation} size="2x"/>
            </Cell>    
            <Cell col={8}>
            <h4 style={{marginTop: '8px'}}>{this.props.jobName}</h4>
            <p>{this.props.jobDescription}</p>
            <p>Tecnologias Usadas: </p>
            <hr style={{borderTop: '3px solid purple'}} />
            {this.props.listItem.map((icon) => <ItemIcon key={icon.key} icon={icon.icon} message={icon.message} />)}
            <ExtraIcons refIcon={this.props.extraIcon.refIcon} needIcon={this.props.extraIcon.needIcon} textIcon={this.props.extraIcon.textIcon}   > </ExtraIcons>
            </Cell>    
        </Grid>
    </div>
)

}

}


export default Experience;